
.overlay {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1;
  
  .drawer {
    position: absolute;
    width: 420px;
    height: 92%;
    right: 0;
    top: 0;
    padding: 30px;
    background: #ffffff;
    box-shadow: -10px 4px 24px(0, 0, 0, 0.1);
    display: flex;
    flex-direction: column;
    overflow-y: scroll;

    h2 {
      margin: 0;
      font-size: 24px;
      display: flex;
      justify-content: space-between;

      img {
        cursor: pointer;
      }

      .removeBtn {
        opacity: 0.5;
        cursor: pointer;
        transition: opacity 0.5s ease-in-out;

        &:hover {
          opacity: 1;
        }
      }
    }

    .cartEmpty {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      height: 100%;
      position: relative;

      .box {
        padding: 20px;
        margin: 20px;
        width: 120px;
        height: 120px;
      }

      p {
        opacity: 0.6;
        margin: 20px;
      }
    }

    .wrapper {
      display: flex;
      flex-direction: column;
      flex: 1;

      .items {
        display: flex;
        overflow: auto;
        margin-top: 30px;
        margin-bottom: 30px;
        flex-direction: column;
        flex: 1 ;
        .cartItem {
          display: flex;
          align-items: center;
          border: 1px solid #F3F3F3;
          border-radius: 20px;
          overflow: hidden;
          padding: 20px;
          margin-top: 20px;

          .cartItemImg {
            height: 70px;
            width: 70px;
            background-size: contain;
            background-position: 0 -3px;
            background-repeat: no-repeat;
            margin-right: 20px;

            img {
              margin-right: 20px;
            }
          }

          .cartDescribe {
            margin-right: 20px;
            flex: 1;

            p {
              font-size: 16px;
            }

            b {
              font-size: 14px;
            }
          }

          .removeBtn {
            opacity: 0.5;
            cursor: pointer;
            transition: opacity 0.5s ease-in-out;

            &:hover {
              opacity: 1;
            }
          }
        }


      }

      .cartTotalBlock {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;

        ul {
          padding: 0;
          margin: 0;
        }

        li {
          display: flex;
          align-items: flex-end;
          margin-bottom: 20px;
        }

        div {
          flex: 1;
          height: 1px;
          border-bottom: 1px dashed #DFDFDF;
          position: relative;
          top: -3px;
          margin: 0 7px;
        }


      }
    }



    .greenButton {
      position: relative;
      width: 100%;
      height: 55px;
      background: #9DD558;
      border-radius: 18px;
      border: 0;
      color: white;
      font-size: 16px;
      font-weight: 500;
      cursor: pointer;


      &:hover {
        background: lighten($color: #a7f448, $amount: 5%);
      }

      &:active {
        background: darken($color: #a7f448, $amount: 5%);
      }

      &:hover {
        img {
          transform: translateX(5px);
        }
      }

      &:disabled {
        background-color: #DFDFDF;
        cursor: default;
      }
    }
  }

}

@keyframes button-loading {
  0% {
    opacity: 0;
  }

  50% {
    opacity: 0.7;
  }

  100% {
    opacity: 1;
  }
}